@import url(https://fonts.googleapis.com/css?family=Farro|Lato|Long+Cang&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App{text-align:center;margin-left:auto;margin-right:auto;background-color:white;margin-bottom:50px}#greeting{font-family:"Long Cang",cursive;font-size:60px}body{font-family:"Lato",sans-serif}h1,h2{font-family:"Farro",sans-serif}nav{display:flex;justify-content:space-around;align-items:center;min-height:10vh;background:#E2964A;color:white}.nav-links{list-style:none;width:50%;display:flex;justify-content:space-between;align-items:center}.link{position:relative;font-size:16px}.link:hover{font-weight:bold;-webkit-transition:all 0.3s ease-in-out 0s;transition:all 0.3s ease-in-out 0s}.link:before{content:"";position:absolute;width:100%;height:2px;bottom:0;left:0;background-color:white;visibility:hidden;-webkit-transform:scaleX(0);transform:scaleX(0);-webkit-transition:all 0.3s ease-in-out 0s;transition:all 0.3s ease-in-out 0s}.link:focus{-webkit-text-decoration:underline white solid;text-decoration:underline white solid}.link:hover:before{visibility:visible;-webkit-transform:scaleX(1);transform:scaleX(1)}.text-container{margin:auto;max-width:700px;min-width:350px}.project-card{height:300px;box-shadow:5px 5px 15px rgba(0,0,0,0.5);margin-top:50px;display:flex;flex-direction:column;justify-content:space-between;overflow:hidden}.project-card .project-card-overlay{height:200px;position:relative;left:0;background-color:rgba(96,165,149,0.5);overflow:hidden;height:100%;-webkit-transform:translateX(-100%);transform:translateX(-100%);-webkit-transition:.5s ease;transition:.5s ease;display:flex;flex-direction:column;justify-content:center}.project-card .project-name{margin-bottom:0}.project-card .project-description{padding:0 20px 0 20px;margin:0}.project-card:hover .project-card-overlay{-webkit-transform:translateX(0);transform:translateX(0)}.project-card button{padding:5px;margin:10px}

